@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;600&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #61dbfb;
  font-family: "Roboto Mono", monospace;
  color: aliceblue;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.App > h1 {
  width: 280px;
  font-size: 28px;
  margin: 10px 0;
  text-align: center;
  color: aliceblue;

  padding: 5px 0;
  background: rgba(0, 0, 0, 0.4);
  box-shadow: 0 8px 32px 0 #1e436e5e;
  backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
  border-radius: 10px;
}

.bands {
  display: flex;
  width: 280px;
  background: rgba(0, 0, 0, 0.4);
  box-shadow: 0 8px 32px 0 #1e436e5e;
  backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
  border-radius: 10px;
}

.bands p {
  text-align: center;
  width: calc(280px / 4);
  padding: 10px 0;

  background-color: transparent;

  cursor: pointer;
  font-size: 12px;
}

.bands p:nth-child(1) {
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}
.bands p:last-child {
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.bands .active {
  background: rgba(0, 0, 0, 0.4);
  border-radius: 0;
}

.calculated-value {
  width: 260px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0 auto;
  flex-wrap: wrap;
  padding: 10px 0;
  background: rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(11px);
  -webkit-backdrop-filter: blur(11px);
  border-radius: 10px;
  margin-top: 20px;
  color: aliceblue;
}

.calculated-value h3 {
  font-size: 20px;
  background-color: transparent;
  text-align: center;
  margin-bottom: 10px;
}
.calculated-value h1 {
  background-color: transparent;
  text-align: center;
  margin-bottom: 10px;
}
.calculated-value p {
  background-color: transparent;
}
.calculated-value p {
  text-align: center;
  margin: 10px 0;
}

.model {
  box-sizing: content-box;
  width: 260px;
  display: flex;
  margin: 0 auto;
  flex-wrap: wrap;
  padding: 10px 0;
  background: rgba(0, 0, 0, 0.15);
  backdrop-filter: blur(11px);
  -webkit-backdrop-filter: blur(11px);
  border-radius: 10px;
  border: 1px solid #00aa39;
  color: aliceblue;
}

.model > h4 {
  width: 100%;
  text-align: center;
  background-color: transparent;
  margin-bottom: 10px;
  font-size: 14px;
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: calc(260px / 4);
  margin: 5px 0;
  background-color: transparent;
}

.container > p {
  font-size: 10px;
  margin-top: 4px;
  background-color: transparent;
}

.color {
  width: 20px;
  height: 20px;
  border-radius: 4px;
  cursor: pointer;
}

.resistor {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 280px;
  padding: 20px 0;
  margin-top: 20px;
  background: rgba(0, 0, 0, 0.4);
  box-shadow: 0 8px 32px 0 #1e436e5e;
  backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
  border-radius: 10px;
}
.resistor svg {
  background-color: transparent;
}

.color-box {
  cursor: pointer;
}

.remove {
  display: none;
}

